const menuIcon = document.getElementById("nav__menu_logo");
const closeMenuIcon = document.getElementById("nav__menu_close");
const navMenu = document.getElementById("mobile_nav");

menuIcon.addEventListener("click", () => {
  navMenu.style.display = "block";
  navMenu.style.transform = "translateX(0)";
});

closeMenuIcon.addEventListener("click", () => {
  navMenu.style.transform = "translateX(-30rem)";
  //   navMenu.style.display = "none";
});
